import { useRef } from "react"

export const FormTest = () => {

    const formRef = useRef();

    const handleSubmit = (e) => {
        const data = new FormData(formRef.current);
        console.log(data);
    }
    return (
        <form onSubmit={handleSubmit}>
            <input name="name"/>
            <input name="email"/>
            <input value={"submit"}/>
        </form>
    )
}